<style scoped>
    .menu{
        float:left;
        width:225px;
        padding:10px 10px 10px 0;
        overflow:hidden;
        background:#3d3d3d;
        transition-duration:.3s;
        color:white;
        &.cur{ width:35px; }
        .sidebar{
            text-align:right;
            i{
                width:29px; height:29px;
                opacity:.7;
                cursor:pointer;
                background:url("../../images/menu-icon.jpg") no-repeat;
                &:hover{opacity:1;}
            }
        }
    }
</style>
<template>
    <article class="menu" :class="{cur:!show}">
        <div class="sidebar">
            <i class="icon" @click="setMenu"></i>
        </div>
        <ul class="page-menu">
            <li><i class="fa fa-angle-right" aria-hidden="true"></i>
                <span class="title">栏目名称</span>
                <span class="arrow"><i class="fa fa-angle-right" aria-hidden="true"></i></span>
            </li>
        </ul>
    </article>
</template>
<script>
	export default {
		props : {
        },
		data() {
			return {
				width : 225,
				show : true, //是否展示栏目
            }
		},
		mounted() {

		},
		methods : {
			setMenu(){
                this.show = !this.show;
            }
        }
	}
</script>


